.header {
  position: relative;
  background: url(../images/hero-back.jpg) no-repeat center / cover;
}
.header:after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 50%;
  transform: translateX(-50%);
  width: 164px;
  height: 50px;
  background: url(../images/decor-section-header.png) no-repeat;
}
.header__top {
  padding: 10px 0;
  background: #0b355b;
}
.header__box {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__item {
  margin-right: 35px;
  list-style: none;
}
.header__link {
  position: relative;
  padding-bottom: 5px;
}
.header__link:after {
  content: '';
  position: absolute;
  bottom: 0px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #abc12e;
  transition: transform .3s  linear;
  transform: scaleX(0);
  transform-origin: bottom right;
  z-index: 10;
}
.header__link:hover {
  transition: all .5s;
}
.header__link:hover:after {
  -webkit-transform-origin: bottom left;
  -ms-transform-origin: bottom left;
  transform-origin: bottom left;
  transform: scaleX(1);
}
.header__phone {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 180px;
}
.header__center {
  text-align: center;
