/* RESPONSIVE MENU TO 992px*/
.open {
  transform: translateX(-220px);
  transition-duration: 0.5s;
  position: relative;
  z-index: 100;
}
@media only screen and (min-width: 992px) {
  .open {
    display: none;
  }
}
.overlay {
  position: fixed;
  width: 220px;
  background: #f4f6f6;
  height: 100vh;
  top: 60px;
  right: -220px;
  overflow-y: scroll;
  transition: all .3s linear;
}
@media only screen and (min-width: 992px) {
  .overlay {
    position: inherit;
    width: inherit;
    height: inherit;
    background: inherit;
    overflow-y: inherit;
  }
}
.burger {
  position: relative;
  width: 50px;
  height: 50px;
  transition: all 0.6s ease-in-out;
  z-index: 1000;
  background: transparent;
  border: none;
  outline: none;
  display: block;
  z-index: 100;
}
.burger:hover {
  cursor: pointer;
}
@media only screen and (min-width: 992px) {
  .burger {
    display: none;
  }
}
.burger i {
  display: block;
  width: 40px;
  height: 2px;
  background: yellow;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.2s ease-in-out;
}
.burger i:before,
.burger i:after {
  content: '';
  position: absolute;
  height: 2px;
  background: yellow;
  width: 40px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.2s ease-in-out;
}
.burger i:before {
  margin-top: -12px;
}
.burger i:after {
  margin-top: 12px;
}
.burger:hover i:before {
  margin-top: -14px;
}
.burger:hover i:after {
  margin-top: 14px;
}
.burger.active i {
  background: transparent;
}
.burger.active i:before,
.burger.active i:after {
  left: -5px;
  width: 50px;
}
.burger.active i:before {
  transform: rotate(45deg);
  top: 12px;
}
.burger.active i:after {
  transform: rotate(-45deg);
  top: -12px;
}
.burger.active:hover i:before,
.burger.active:hover i:after {
  width: 60px;
  margin: 0;
  left: -10px;
  top: 0;
}
/* END RESPONSIVE MENU TO 992px*/
.slick-prev,
.slick-next {
  top: 50%;
  transform: translateY(-50%);
  width: 50px;
  height: 100%;
  opacity: 1;
  transition: opacity .3s linear;
  z-index: 10;
}
.slick-prev {
  left: -35px;
  width: 29px;
  height: 45px;
  background: url('../images/icons/css_sprites.png') -59px -154px;
}
.slick-next {
  right: -35px;
  width: 29px;
  height: 45px;
  background: url('../images/icons/css_sprites.png') -108px -154px;
}
.slick-prev:before,
.slick-next:before {
  width: 0;
  height: 0;
}
.slick-prev:focus,
.slick-next:focus {
  opacity: 1;
}
.slick-disabled {
  opacity: .45!important;
}
.slick-dots {
  display: inline-block !important;
  left: 50%;
  transform: translateX(-50%);
  bottom: -20px;
  z-index: 20;
}
@media only screen and (min-width: 768px) {
  .slick-dots {
    bottom: -40px;
  }
}
.slick-dots li {
  margin: 7px;
  width: 13px;
  height: 13px;
}
.slick-dots li button {
  background: #000;
  display: block;
  height: 5px;
  width: 5px;
  font-size: 0px;
  border-radius: 50%;
  border: 1px solid transparent;
  opacity: 0.75;
  transition: all .3s linear;
}
.slick-dots li button:before {
  font-size: 0;
  opacity: 0;
  display: none;
}
.slick-dots li.slick-active button {
  opacity: 1;
  transform: scale(2);
  border: 1px solid #000;
  background: transparent;
}
.slick-current,
.slick-slide,
.slick-active {
  outline: none!important;
}
